.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }
  
  .error,
  .loading {
    color: #ff6b6b;
    font-size: 18px;
  }
  
  .movieDetails {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .movieTitle {
    font-size: 24px;
    color: #333;
    margin: 20px 0;
  }
  
  .movieImage {
    max-width: 20vh;
    height: auto;
    border-radius: 8px;
  }
  
  .movieOverview {
    font-size: 16px;
    color: #666;
    margin-top: 20px;
  }
  
  .button {
    text-decoration: none;
    background-color: #333;
    color: white;
    padding: 10px 15px;
    margin: 5px;
    border-radius: 4px;
    transition: background-color 0.3s ease;
  }
  
  .button:hover {
    background-color: rgb(207, 207, 42);
  }